@import "./styles/Topnav.css";
@import "./styles/Hero.css";
@import "./styles/Cases.css";
@import "./styles/Footer.css";
@import "./styles/Projects.css";
 

@font-face {
  font-family: 'NeueHaasDisplayRoman';
  src: local('NeueHaasDisplayRoman'), url(./styles/fonts/NeueHaasDisplay-Roman.woff) format('truetype');
}

@font-face {
  font-family: 'EditorialNewUltralight';
  src: local('EditorialNew-Ultralight'), url(./styles/fonts/EditorialNew-Ultralight.woff) format('truetype');
}

@font-face {
  font-family: 'EditorialNewUltralightItalic';
  src: local('EditorialNew-UltralightItalic'), url(./styles/fonts/EditorialNew-UltralightItalic.woff) format('truetype');
}

body,
html {
  overflow-y: auto;
  user-select: none;
  background-color: white;
  overscroll-behavior: none;
  margin: 0;
  font-family: "NeueHaasDisplayRoman", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}


.noselect {
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:none;
  -o-user-select:none;
   user-select:none;
   pointer-events:none
}

video {
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:none;
  -o-user-select:none;
   user-select:none;
   pointer-events:none
}

#main {
  height: 100vh;
 }