header{
    padding: 1.25rem 1.25rem;
    width: 100vw;
    position: fixed;
    z-index: 10; 
    mix-blend-mode: difference;
}

nav{
    display: flex;
    justify-content: space-between;
}

a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-family: "NeueHaasDisplayRoman", "Helvetica Neue", sans-serif;
    font-size: 1.5rem;
    font-weight: 55;
}

.left{
    width: 20%;
}

.left a{
    margin-right: 1rem;
}

.right{
    width: 20%;
    text-align: right;
}

.center{
    width: 20%;
    text-align: center;
}